'use client'
import { useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link'
import Swal from 'sweetalert2'

export default function Page() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const router = useRouter();

  const handleLogin = async (e) => {
    e.preventDefault();

    const res = await fetch('https://backend-nextjs-virid.vercel.app/api/auth/login', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ username, password }),
    });

    const data = await res.json();
    console.log(username);

    if (data.token) {
      // Use in-memory storage instead of localStorage for this demo
      sessionStorage.setItem('token', data.token);
      Swal.fire({
        icon: 'success',
        title: '<h3 style="color: #dc3545; font-family: \'Cinzel\', serif;">戦士の帰還 - Warrior\'s Return</h3>',
        html: '<p style="color: #6c757d;">Login Successfully!</p>',
        background: '#1a1a1a',
        showConfirmButton: false,
        timer: 2000
      }).then(function () {
        router.push('/admin/users');
      });
    } else {
      Swal.fire({
        icon: 'warning',
        title: '<h3 style="color: #dc3545; font-family: \'Cinzel\', serif;">失敗 - Failed</h3>',
        html: '<p style="color: #6c757d;">Login Failed!</p>',
        background: '#1a1a1a',
        showConfirmButton: false,
        timer: 2000
      }).then(function () {
        router.push('/signin');
      });
    }
  };

  return (
    <>
      <style jsx>{`
        @import url('https://fonts.googleapis.com/css2?family=Cinzel:wght@400;600;700&family=Noto+Sans+JP:wght@300;400;500&display=swap');
        
        body {
          background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 50%, #2d1b1b 100%);
          min-height: 100vh;
          font-family: 'Noto Sans JP', sans-serif;
          overflow-x: hidden;
        }
        
        .samurai-bg {
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: 
            radial-gradient(circle at 20% 50%, rgba(220, 53, 69, 0.1) 0%, transparent 50%),
            radial-gradient(circle at 80% 20%, rgba(220, 53, 69, 0.05) 0%, transparent 50%),
            radial-gradient(circle at 40% 80%, rgba(220, 53, 69, 0.08) 0%, transparent 50%),
            linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 50%, #2d1b1b 100%);
          z-index: -2;
        }
        
        .samurai-pattern {
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-image: 
            repeating-linear-gradient(
              45deg,
              transparent,
              transparent 100px,
              rgba(220, 53, 69, 0.01) 100px,
              rgba(220, 53, 69, 0.01) 101px
            );
          z-index: -1;
          animation: drift 20s ease-in-out infinite;
        }
        
        @keyframes drift {
          0%, 100% { transform: translateX(0px) translateY(0px); }
          25% { transform: translateX(10px) translateY(-5px); }
          50% { transform: translateX(-5px) translateY(10px); }
          75% { transform: translateX(-10px) translateY(-5px); }
        }
        
        .container {
          position: relative;
          z-index: 1;
        }
        
        .samurai-card {
          background: rgba(13, 13, 13, 0.95);
          border: 2px solid #dc3545;
          border-radius: 15px;
          box-shadow: 
            0 0 30px rgba(220, 53, 69, 0.3),
            inset 0 1px 0 rgba(255, 255, 255, 0.1);
          backdrop-filter: blur(10px);
          position: relative;
          overflow: hidden;
          animation: cardGlow 3s ease-in-out infinite alternate;
        }
        
        @keyframes cardGlow {
          0% { box-shadow: 0 0 30px rgba(220, 53, 69, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.1); }
          100% { box-shadow: 0 0 40px rgba(220, 53, 69, 0.5), inset 0 1px 0 rgba(255, 255, 255, 0.1); }
        }
        
        .samurai-card::before {
          content: '';
          position: absolute;
          top: -2px;
          left: -2px;
          right: -2px;
          bottom: -2px;
          background: linear-gradient(45deg, #dc3545, #000, #dc3545, #000);
          z-index: -1;
          border-radius: 15px;
          animation: borderRotate 4s linear infinite;
        }
        
        @keyframes borderRotate {
          0% { transform: rotate(0deg); }
          100% { transform: rotate(360deg); }
        }
        
        .card-header-samurai {
          background: linear-gradient(135deg, #dc3545 0%, #a71e2a 100%);
          color: white;
          text-align: center;
          font-family: 'Cinzel', serif;
          font-weight: 700;
          font-size: 1.5rem;
          text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
          position: relative;
          border-radius: 13px 13px 0 0;
          padding: 20px;
        }
        
        .card-header-samurai::after {
          content: '⚔️';
          position: absolute;
          right: 20px;
          top: 50%;
          transform: translateY(-50%);
          font-size: 1.2rem;
          animation: swordGlint 2s ease-in-out infinite;
        }
        
        @keyframes swordGlint {
          0%, 100% { opacity: 0.7; transform: translateY(-50%) rotate(0deg); }
          50% { opacity: 1; transform: translateY(-50%) rotate(5deg); }
        }
        
        .card-body-samurai {
          background: rgba(26, 26, 26, 0.9);
          padding: 30px;
          border-radius: 0 0 13px 13px;
        }
        
        .form-label-samurai {
          color: #dc3545;
          font-weight: 600;
          font-family: 'Cinzel', serif;
          text-transform: uppercase;
          letter-spacing: 1px;
          margin-bottom: 10px;
        }
        
        .input-group-samurai {
          margin-bottom: 25px;
        }
        
        .input-group-text-samurai {
          background: linear-gradient(135deg, #dc3545 0%, #a71e2a 100%);
          border: 2px solid #dc3545;
          color: white;
          border-radius: 8px 0 0 8px;
          min-width: 50px;
          display: flex;
          align-items: center;
          justify-content: center;
        }
        
        .form-control-samurai {
          background: rgba(13, 13, 13, 0.8);
          border: 2px solid #444;
          border-left: none;
          color: #fff;
          border-radius: 0 8px 8px 0;
          padding: 12px 15px;
          font-family: 'Noto Sans JP', sans-serif;
          transition: all 0.3s ease;
        }
        
        .form-control-samurai:focus {
          background: rgba(13, 13, 13, 0.9);
          border-color: #dc3545;
          box-shadow: 0 0 15px rgba(220, 53, 69, 0.3);
          color: #fff;
        }
        
        .form-control-samurai::placeholder {
          color: #888;
          font-style: italic;
        }
        
        .btn-samurai {
          background: linear-gradient(135deg, #dc3545 0%, #a71e2a 100%);
          border: 2px solid #dc3545;
          color: white;
          padding: 12px 30px;
          font-family: 'Cinzel', serif;
          font-weight: 600;
          text-transform: uppercase;
          letter-spacing: 2px;
          border-radius: 8px;
          width: 100%;
          position: relative;
          overflow: hidden;
          transition: all 0.3s ease;
        }
        
        .btn-samurai::before {
          content: '';
          position: absolute;
          top: 0;
          left: -100%;
          width: 100%;
          height: 100%;
          background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
          transition: left 0.5s;
        }
        
        .btn-samurai:hover::before {
          left: 100%;
        }
        
        .btn-samurai:hover {
          background: linear-gradient(135deg, #e63946 0%, #b71c2c 100%);
          transform: translateY(-2px);
          box-shadow: 0 5px 20px rgba(220, 53, 69, 0.4);
        }
        
        .samurai-links {
          text-align: center;
          margin-top: 20px;
        }
        
        .samurai-link {
          color: #dc3545;
          text-decoration: none;
          font-family: 'Cinzel', serif;
          font-weight: 500;
          transition: all 0.3s ease;
          position: relative;
        }
        
        .samurai-link::after {
          content: '';
          position: absolute;
          width: 0;
          height: 2px;
          bottom: -2px;
          left: 50%;
          background: #dc3545;
          transition: all 0.3s ease;
        }
        
        .samurai-link:hover {
          color: #e63946;
          text-shadow: 0 0 8px rgba(220, 53, 69, 0.5);
        }
        
        .samurai-link:hover::after {
          width: 100%;
          left: 0;
        }
        
        .divider {
          color: #666;
          margin: 0 10px;
        }
        
        .japanese-text {
          position: absolute;
          top: -10px;
          right: 20px;
          color: rgba(220, 53, 69, 0.6);
          font-size: 0.9rem;
          font-family: 'Noto Sans JP', sans-serif;
          font-weight: 300;
        }
        
        @media (max-width: 768px) {
          .samurai-card {
            margin: 10px;
          }
          
          .card-header-samurai {
            font-size: 1.2rem;
            padding: 15px;
          }
          
          .card-body-samurai {
            padding: 20px;
          }
        }
      `}</style>
      
      <div className="samurai-bg"></div>
      <div className="samurai-pattern"></div>
      
      <div style={{ paddingTop: '100px', paddingBottom: '50px', minHeight: '100vh' }}>
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-md-6 col-lg-5">
              <div className="card samurai-card">
                <div className="card-header card-header-samurai">
                  <span className="japanese-text">武士の道</span>
                  侍 Login Portal
                </div>
                <div className="card-body card-body-samurai">
                  <form className="row g-3" onSubmit={handleLogin}>
                    <div className="col-12">
                      <label className="form-label form-label-samurai">⚔️ Username</label>
                      <div className="input-group input-group-samurai">
                        <span className="input-group-text input-group-text-samurai">
                          <i className="bi bi-person-vcard" style={{ fontSize: '1.1rem' }}></i>
                        </span>
                        <input 
                          type="text" 
                          className="form-control form-control-samurai" 
                          placeholder="Enter your warrior name" 
                          value={username}
                          onChange={(e) => setUsername(e.target.value)} 
                        />
                      </div>
                    </div>
                    
                    <div className="col-12">
                      <label className="form-label form-label-samurai">🗡️ Password</label>
                      <div className="input-group input-group-samurai">
                        <span className="input-group-text input-group-text-samurai">
                          <i className="bi bi-shield-lock" style={{ fontSize: '1.1rem' }}></i>
                        </span>
                        <input 
                          type="password" 
                          className="form-control form-control-samurai" 
                          placeholder="Enter your secret code" 
                          value={password}
                          onChange={(e) => setPassword(e.target.value)} 
                        />
                      </div>
                    </div>
                    
                    <div className="col-12">
                      <button type="submit" className="btn btn-samurai">
                        Enter the Dojo
                      </button>
                    </div>
                    
                    <div className="col-12 samurai-links">
                      <Link href="/register" className="samurai-link">Create Account</Link>
                      <span className="divider">|</span>
                      <Link href="/" className="samurai-link">Forget Password</Link>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}